.datatable {
    height: 600px;
    padding: 20px;
}
.datatable .datatableTitle {
    width: 100%;
    font-size: 24px;
    color: gray;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
 table {
          border-collapse: collapse;
          width: 100%;
          margin-bottom: 20px;
        }
        th, td {
          border: 1px solid black;
          padding: 8px;
          text-align: left;
          vertical-align: top;
        }
        th {
          background-color: #ddd;
          font-weight: bold;
        }
        h2 {
          font-size: 24px;
          margin-top: 40px;
          margin-bottom: 10px;
        }
        .container {
            display: flex;
            align-items: center;
        }

        .image {
            width: 50%;
            height: auto;
            margin-right: 20px;
        }

        #p {
            width: 50%;
            height: 300px;
            font-size: 16px;
            padding: 10px;
            font-size: 50px;
        }
.datatable .datatableTitle .link {
    text-decoration: none;
    color: green;
    font-size: 16px;
    font-weight: 400;
    border: 1px solid green;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
}
.datatable .cellWithImg {
    display: flex;
    align-items: center;
}
.datatable .cellWithImg .cellImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}
.datatable .cellWithStatus {
    padding: 5px;
    border-radius: 5px;
}
.datatable .cellWithStatus.active {
    background-color: rgba(0, 128, 0, 0.05);
    color: green;
}
.datatable .cellWithStatus.pending {
    background-color: rgba(255, 217, 0, 0.05);
    color: goldenrod;
}
.datatable .cellWithStatus.passive {
    background-color: rgba(255, 0, 0, 0.05);
    color: crimson;
}
.datatable .cellAction {
    display: flex;
    align-items: center;
    gap: 15px;
}
.datatable .cellAction .viewButton {
    padding: 2px 5px;
    border-radius: 5px;
    color: darkblue;
    border: 1px dotted rgba(0, 0, 139, 0.596);
    cursor: pointer;
}
.datatable .cellAction .deleteButton {
    padding: 2px 5px;
    border-radius: 5px;
    color: crimson;
    border: 1px dotted rgba(220, 20, 60, 0.6);
    cursor: pointer;
}
td{
    height: 100px;
}